import { Box, LinearProgress, Typography } from "@mui/material"
import React from "react"
import AdvancedLoading from "./toys/advancedLoading"
import { Suspense } from "react"
import { lazy } from "react"
import { useMemo } from "react"
import { useState } from "react"
import { createPortal } from "react-dom"

const LazyEditor = () => {

  const [loaded, setLoaded] = useState(false);

  const Editor = useMemo(() => {
    const time1 = performance.now();
    return lazy(() => import('./editor').then(res => {
      console.log('load editor cost', performance.now() - time1, 'ms');
      // mock
      return new Promise<any>(resolve => {
        setTimeout(() => {
          setLoaded(true);
          resolve(res);
        }, 4000 + Math.random() * 5000);
      })
    }));
  }, []);

  return (
    <Box>
      {!loaded && (
        createPortal(
        <Box
          id="gogogo"
          position={'fixed'}
          height={'100%'}
          width={'100%'}
          display={'flex'}
          alignItems={'center'}
          justifyContent={'center'}
          bgcolor={'var(--container-bg-2)'}
          zIndex={1209}
          flexDirection={'column'}
        >
          <AdvancedLoading />
          <LinearProgress
            sx={{
              width: '100%',
              position: 'fixed',
              top: 0,
            }}
          />
          <Typography mt={10} fontSize={18} fontWeight={900}>Welcome</Typography>
        </Box>, document.getElementById('root')!)
      )}
      <Suspense>
        <Editor />
      </Suspense>
    </Box>
  )
}

export default LazyEditor;